import React, { useEffect, useState } from 'react';
import axios from 'axios'
import FacebookLogin from 'react-facebook-login';
import logo from './logo.svg';
import './App.css';

const Auth = (props) => {
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    let src = document.location.href.split("#access_token=");
    if (src.length > 1) {
      setLoading(true);
      let input_token = src[1].split("&")[0];
      props.setAccess_token(input_token);
      axios.get('https://graph.facebook.com/oauth/access_token' +
        '?client_id=664136191119867' +
        '&client_secret=4d68291edb2d58ca22c139c273718fdc' +
        '&grant_type=client_credentials').then((res) => {
          let access_token = res.data.access_token;

          axios.get(`https://graph.facebook.com/v8.0/debug_token?input_token=${input_token}&access_token=${access_token}`).then((res) => {
            let user_id = res.data.data.user_id;
            let name = "";
            let picture = "";

            axios.get(`https://graph.facebook.com/v8.0/${user_id}/picture?redirect=0&access_token=${access_token}`).then((user) => {
              picture = user.data.data.url;
              axios.get(`https://graph.facebook.com/v8.0/${user_id}?access_token=${access_token}`).then((user) => {
                name = user.data.name;
                props.set_user(user_id, name, picture);
              });
            });
          });
        });
    }
  }, []);

  return <div className="auth-cont">
    {loading ?
      <div className="loading">
        <label>Logging in...</label>
        <div className="anim">
          <span className="item first" />
          <span className="item middle" />
          <span className="item middle" />
          <span className="item middle" />
          <span className="item last" />
        </div>
      </div>
      :
      <div className="auth-wrapper">
        <div className="button-wrapper">
          <span className="before" />
          <FacebookLogin
            appId="664136191119867"
            autoLoad={false}
            fields="name,picture"
            version="8.0"
            cssClass="auth-button"
            redirectUri="https://sdidsa.github.io/cover-collab"
            responseType="token"
            onClick={
              e => {
                console.log("clicked");
              }
            }
            callback={
              r => {
                if (r.userID) {
                  setLoading(true);
                  props.setAccess_token(r.accessToken);
                  props.set_user(r.userID, r.name, r.picture.data.url);
                } else {
                  console.log('fail');
                }
              }
            } />
        </div>
        <div className="logo-wrapper">
          <img className="logo" alt="logo" src={logo} />
        </div>
      </div>
    }
  </div>;
}

function getWindowDimensions() {
  const { innerWidth: width } = window;
  return {
    width: width > 800 ? 800 : width
  };
}

const Block = (props) => {
  const getContent = () => {
    for (let i = 0; i < props.positions.length; i++) {
      let pos = props.positions[i];
      if (pos.x === props.x && pos.y === props.y) {
        return pos;
      }
    }
    return null;
  }

  const isSelected = () => {
    if (props.selectedPos && props.selectedPos.x === props.x && props.selectedPos.y === props.y) {
      return true;
    } else {
      return false;
    }
  }

  return <div
    className={`block${getContent() ? "" : " empty"}${isSelected() ? " selected" : ""}`}
    style={{ width: props.blockSize, height: props.blockSize }}
    onClick={(e) => {
      if (isSelected()) {
        props.setSelectedPos(null);
      } else {
        props.setSelectedPos({
          x: props.x,
          y: props.y
        })
      }
    }}>
    {getContent() ? <span class="member-image" style={{ backgroundImage: "url('" + getContent().image.replace(".jpg", "").replace(".png", "") + "l.jpg')" }} /> : ''}
  </div>
}

const Row = (props) => {
  return <div className="row">
    {Array.apply(null, Array(props.cover.width)).map((i, index) => {
      return <Block key={index} setSelectedPos={props.setSelectedPos} selectedPos={props.selectedPos} positions={props.positions} cover={props.cover} blockSize={props.blockSize} x={index} y={props.y} />
    })}
  </div>
}

const Grid = (props) => {
  const [blockSize, setBlockSize] = useState();

  useEffect(() => {
    function handleResize() {
      let tw = getWindowDimensions().width;
      let bs = (tw - ((props.cover.width - 1) * 10)) / props.cover.width;
      setBlockSize(bs);
    }
    handleResize();
    window.addEventListener('resize', handleResize);
  }, []);

  return <div className="grid">
    {Array.apply(null, Array(props.cover.height)).map((i, index) => {
      return <Row key={index} setSelectedPos={props.setSelectedPos} selectedPos={props.selectedPos} positions={props.positions} cover={props.cover} blockSize={blockSize} y={i, index} />
    })}
  </div>
}

const PosDetails = (props) => {
  const isMine = () => {
    return props.details.user_id === props.user.id;
  }

  const canAdd = () => {
    for (let i = 0; i < props.positions.length; i++) {
      let pos = props.positions[i];
      if (pos.user_id === props.user.id) {
        return false;
      }
    }
    return true;
  }

  return <div className="pos-details">
    {props.details.status === "empty" ?
      <div className="empty">
        <span className="actual-image" />
        <div className="info">
          <div className="top">
            <label>Empty Position</label>
          </div>
          <div className="buttons">{canAdd() ?
            <span className="details-button neutral" onClick={(e) => {
              try {
                let input = e.target.childNodes[1].childNodes[0];
                input.click();
              } catch (x) {
                //discard
              }
            }}>Upload
            <form style={{ display: "none" }} action={props.api + 'upload'} method="post" encType="multipart/form-data">
                <input name="file" type="file" onChange={(e) => {
                  e.target.parentNode.submit();
                }} />
                <input name="uid" defaultValue={props.user.id} />
                <input name="cid" defaultValue={props.coverid} />
                <input name="x" defaultValue={props.details.x} />
                <input name="y" defaultValue={props.details.y} />
                <input name="access_token" defaultValue={props.access_token} />
              </form></span>
            :
            "you already added an image to the cover (you can delete it)"}
          </div>
        </div>
      </div>
      :
      <div className="taken">
        <span className="actual-image member-image" style={{ backgroundImage: "url('" + props.details.image.replace(".jpg", "").replace(".png", "") + "l.jpg')" }} />
        <div className="info">
          <div className="top">
            <span className="profile-pic member-image" style={{ backgroundImage: "url('" + props.details.image_URL + "')" }} />
            <div className="name">
              <label>uploaded by {isMine() ? '(you)' : ''}</label>
              <label className="username">{props.details.name}</label>
            </div>
          </div>
          {isMine() ? <div className="buttons">
            <span style={{ opacity: 0, pointerEvenets: "none" }} className="details-button neutral">Change Picture</span>
            <span className="details-button bad" onClick={props.remove}>Delete</span>
          </div> : ''}
        </div>
      </div>
    }
  </div>
}

const OpenCover = (props) => {
  const [loading, setLoading] = useState(false);
  const [positions, setPositions] = useState([]);
  const [selectedPos, setSelectedPos] = useState(null);

  useEffect(() => {
    refresh();
    let src = document.location.href.split("#");
    if (src.length > 1) {
      let params = src[1].split("&");
      if (params.length === 4) {
        if (params[2].split("=")[0] === "x") {
          let x = parseInt(params[2].split("=")[1]);
          let y = parseInt(params[3].split("=")[1]);
          setSelectedPos({ x, y });
        }
      }
    }
    setInterval(() => {
      axios.post(props.api + "getCover?id=" + props.cover.id).then((res) => {
        setPositions(res.data);
      });
    }, 2000);
  }, []);

  const refresh = () => {
    setLoading(true);
    axios.post(props.api + "getCover?id=" + props.cover.id).then((res) => {
      setPositions(res.data);
      setLoading(false);
    });
  }

  const remove = () => {
    axios.post(props.api + "delete?user_id=" + props.user.id + "&cover_id=" + props.cover.id).then((r) => {
      refresh();
    })
  }

  const getDetails = () => {
    for (let i = 0; i < positions.length; i++) {
      let pos = positions[i];
      if (pos.x === selectedPos.x && pos.y === selectedPos.y) {
        return pos;
      }
    }
    return { status: "empty", x: selectedPos.x, y: selectedPos.y }
  }

  return <div className={`cover-disp${loading ? ' loading' : ''}`}>
    {loading ? <span className="loadanim">loading</span>
      :
      <div className="disp-root">
        <Grid cover={props.cover} setSelectedPos={setSelectedPos} selectedPos={selectedPos} positions={positions} />
        {selectedPos ? <PosDetails access_token={props.access_token} coverid={props.cover.id} api={props.api} remove={remove} user={props.user} positions={positions} details={getDetails()} /> : ''}
      </div>}
  </div>;
}

const CoverItem = (props) => {
  return <div className={`cover-item ${props.cover.status}`} onClick={
    () => {
      if (props.cover.status === "ongoing") {
        props.setSelectedCover(props.cover);
      }
    }
  }>
    <div className="left">
      <label className="name">Cover {props.cover.id.substring(0, 2)}-{props.cover.id.substring(2, 4)}</label>
      <div className="info">
        <label>size {props.cover.width} x {props.cover.height} ({props.cover.height * props.cover.width} position)</label>
      </div>
    </div>
    <label className="status">{props.cover.status}</label>
  </div>;
}

const Home = (props) => {
  const [covers, setCovers] = useState([]);
  const [selectedCover, setSelectedCover] = useState(null);
  useEffect(() => {
    axios.post(props.api + "listCovers").then((res) => {
      setCovers(res.data);

      let src = document.location.href.split("#");
      if (src.length > 1) {
        let params = src[1].split("&");
        if (params.length === 4) {
          let cid = params[1].split("=")[1];

          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].id === cid) {
              setSelectedCover(res.data[i]);
            }
          }
        }
      }
    })


  }, []);

  return <div className="home">
    <div className="all">
      <div className="bar">
        <div className="wrapper">
          <div className="left">
            <img className="pp" alt="facebook profile" src={props.user.image} />
            <label className="name">{props.user.name}</label>
          </div>
          <img alt="main logo" className="logo" src={logo} />
        </div>
      </div>

      <div className="content">
        {selectedCover ?
          <OpenCover access_token={props.access_token} api={props.api} user={props.user} cover={selectedCover} />
          :
          <div className="list">
            {covers.map((e, index) => {
              return <CoverItem key={index} cover={e} setSelectedCover={setSelectedCover} />
            })}
          </div>
        }
      </div>
    </div>
  </div>
}

let def = { "id": "763719554451891", "name": "Lukas Owen", "image": "https://platform-lookaside.fbsbx.com/platform/profilepic/?asid=763719554451891&height=50&width=50&ext=1600354942&hash=AeT9Agba_RTrtVMV" };
const App = (props) => {
  const [user, setUser] = useState(null);
  const [access_token, setAccess_token] = useState(null);

  const set_user = async (id, name, image) => {
    await axios.post(props.api + "auth", { id, name, image });
    setUser({
      id,
      name,
      image
    });
  }

  return (user ? <Home api={props.api} user={user} access_token={access_token} /> : <Auth setAccess_token={setAccess_token} set_user={set_user} />)
}

export default App;
